}

.custom-control-inline {
    display: inline-flex;
    margin-right: 1rem
}

.custom-control-input {
    position: absolute;
    left: 0;
    z-index: -1;
    width: 1.1rem;
    height: 1.3rem;
    opacity: 0
}

.custom-control-input:checked~.custom-control-label:before {
    color: #fff;
    border-color: #fd4100;
    background-color: #fd4100
}

.custom-control-input:focus~.custom-control-label:before {
    box-shadow: 0 0 0 .2rem #fd410040
}

.custom-control-input:focus:not(:checked)~.custom-control-label:before {
    border-color: #ff9f7e
}

.custom-control-input:not(:disabled):active~.custom-control-label:before {
    color: #fff;
    background-color: #ffc5b1;
    border-color: #ffc5b1
}

.custom-control-input[disabled]~.custom-control-label,
.custom-control-input:disabled~.custom-control-label {
    color: #6c757d
}

.custom-control-input[disabled]~.custom-control-label:before,
.custom-control-input:disabled~.custom-control-label:before {
    background-color: #e9e9f0
}

.custom-control-label {
    position: relative;
    margin-bottom: 0;
    color: #272a37;
    vertical-align: top
}

.custom-control-label.custom-control-label-sm:before {
    top: .375rem;
    left: -1.25rem;
    width: .75rem;
    height: .75rem
}

.custom-control-label.custom-control-label-sm:after {
    top: .375rem;
    left: -1.25rem;
    width: .75rem;
    height: .75rem
}

.custom-control-label:before {
    position: absolute;
    top: .2rem;
    left: -1.6rem;
    display: block;
    width: 1.1rem;
    height: 1.1rem;
    pointer-events: none;
    content: "";
    background-color: #fff;
    border: #dadada solid 2px
}

.custom-control-label:after {
    position: absolute;
    top: .2rem;
    left: -1.6rem;
    display: block;
    width: 1.1rem;
    height: 1.1rem;
    content: "";
    background: no-repeat 50%/50% 50%
}

.custom-checkbox .custom-control-label:before {
    border-radius: 4px
}



.custom-checkbox .custom-control-input:indeterminate~.custom-control-label:before {
    border-color: #fd4100;
    background-color: #fd4100
}


.custom-checkbox .custom-control-input:disabled:checked~.custom-control-label:before {
    background-color: #fd410080
}

.custom-checkbox .custom-control-input:disabled:indeterminate~.custom-control-label:before {
    background-color: #fd410080
}

.custom-radio-sm input[type=radio] {
    flex: 0 0 16px !important;
    width: 16px !important;
    height: 16px !important
}

.custom-radio-sm input[type=radio]:before {
    width: 6px !important;
    height: 6px !important
}

.custom-radio-sm input[type=radio]:checked {
    border-width: 2px
}

.custom-radio-checkbox input[type=radio]:before {
    width: 19px !important;
    height: 19px !important
}

.custom-radio-checkbox input[type=radio]:checked:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: no-repeat 50%/50% 50%;
}

.custom-switch {
    padding-left: 2.425rem
}

.custom-switch .custom-control-label:before {
    left: -2.425rem;
    width: 1.925rem;
    pointer-events: all;
    border-radius: .55rem
}

.custom-switch .custom-control-label:after {
    top: calc(.2rem + 4px);
    left: calc(-2.425rem + 4px);
    width: calc(1.1rem - 8px);
    height: calc(1.1rem - 8px);
    background-color: #dadada;
    border-radius: .55rem;
    transition: transform .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out
}

@media (prefers-reduced-motion: reduce) {
    .custom-switch .custom-control-label:after {
        transition: none
    }
}

.custom-switch .custom-control-input:checked~.custom-control-label:after {
    background-color: #fff;
    transform: translate(.825rem)
}

.custom-switch .custom-control-input:disabled:checked~.custom-control-label:before {
    background-color: #fd410080
}

.custom-select {
    display: inline-block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem 1.75rem .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #444;
    vertical-align: middle;
    border: 1px solid #dadada;
    border-radius: 10px;
    appearance: none
}

.custom-select:focus {
    border-color: #ff9f7e;
    outline: 0;
    box-shadow: 0 0 0 .2rem #fd410040
}

.custom-select:focus::-ms-value {
    color: #444;
    background-color: #fff
}

.custom-select[multiple],
.custom-select[size]:not([size="1"]) {
    height: auto;
    padding-right: .75rem;
    background-image: none
}

.custom-select:disabled {
    color: #6c757d;
    background-color: #e9e9f0
}

.custom-select::-ms-expand {
    display: none
}

.custom-select:-moz-focusring {
    color: #0000;
    text-shadow: 0 0 0 #444
}

.custom-select-sm {
    height: calc(1.5em + .5rem + 2px);
    padding-top: .25rem;
    padding-bottom: .25rem;
    padding-left: .5rem;
    font-size: .875rem
}

.custom-select-lg {
    height: calc(1.5em + 1rem + 2px);
    padding-top: .5rem;
    padding-bottom: .5rem;
    padding-left: 1rem;
    font-size: 1.25rem
}

.custom-file {
    position: relative;
    display: inline-block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    margin-bottom: 0
}

.custom-file-input {
    position: relative;
    z-index: 2;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    margin: 0;
    opacity: 0
}

.custom-file-input:focus~.custom-file-label {
    border-color: #ff9f7e;
    box-shadow: 0 0 0 .2rem #fd410040
}

.custom-file-input[disabled]~.custom-file-label,
.custom-file-input:disabled~.custom-file-label {
