}

.custom-file-label:after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    display: block;
    height: calc(1.5em + .75rem);
    padding: .375rem .75rem;
    line-height: 1.5;
    color: #444;
    content: "Browse";
    background-color: #e9e9f0;
    border-left: inherit;
    border-radius: 0 10px 10px 0
}

